.saloon-tables {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 70vh;
  .form-control,
  .form-control label,
  h3 {
    margin-bottom: 0.5rem;
    color: bisque;
  }
  span {
    color: green;
    margin: 0 0.5rem;
    font-size: 0.65rem;
  }
  #query {
    width: 80%;
    height: 1.8rem;
    font-size: 1.2rem;
  }
}
.tables-container {
  width: 35vw;
  border-radius: 1rem;
  overflow: hidden;
  overflow-y: auto;
  margin-top: 0.5rem;
  @media only screen and (min-width: 900px) {
    height: 100%;
  }
}
.available-table {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background-color: bisque;
  border-radius: 10px;
  height: 2.3rem;
  font-weight: 600;
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  margin-bottom: 2px;
  border: 2px solid black;
  box-shadow: inset 0px 0px 13px 0px #000000;
  &.joined {
    background-color: rgb(45, 125, 70);
    span {
      color: red;
    }
  }
  &:hover {
    background-color: #f56700;
    .tool-tip {
      visibility: visible;
      left: 0;
      bottom: 30%;
    }
  }
}
