@use '../../colors';

.checkbox {
  -webkit-appearance: none;
  appearance: none;
  cursor: pointer;
  position: relative;
  height: 1rem;
  width: 1rem;
  background-color: #4c5152;
  border: 2px solid black;
  border-radius: 5px;
  &:after {
    display: none;
    position: absolute;
    content: '';
    left: 50%;
    top: 50%;
    translate: -50% -60%;
    width: 0.3rem;
    height: 0.5rem;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  &:checked {
    background-color: colors.$accent-color;
    &:after {
      display: block;
    }
    &:hover {
      background-color: colors.$accent-color;
    }
  }
  &:hover {
    background-color: #ccc;
  }
}
.form-control:hover .tool-tip {
  visibility: visible;
  &:hover {
    visibility: hidden;
  }
}
