.status {
  position: relative;
  justify-self: center;
  width: 0.7rem;
  height: 0.7rem;
  border-radius: 50%;
  display: inline-block;
  cursor: help;
  .tool-tip {
    left: 50%;
    translate: -50%;
  }
  &:hover .tool-tip {
    visibility: visible;
    &:hover {
      visibility: hidden;
    }
  }
}
