@use '../colors.scss';
@use '../variables.scss';

.forgot,
.change-password {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: colors.$sand-tan-shadow;
  border-radius: variables.$pages-curves;
  height: 100%;
  width: 100%;
  form {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.3rem;
  }
  .frame {
    width: 45%;
    background-color: #2a3236;
  }
  label {
    font-size: 0.8rem;
  }
  input {
    height: 2rem;
    border-radius: 7px;
    background-color: bisque;
    font-weight: 600;
    font-size: 1rem;
  }
  h3 {
    color: #f56700;
  }
  span {
    color: red;
    font-weight: 600;
  }
  button {
    padding: 0.8rem;
    font-weight: 600;
  }
}
