@use '../../../variables.scss';
@use '../../../colors.scss';

.subscription {
  display: grid;
  place-items: center;
  height: 100%;
  width: 100%;
  background-color: colors.$sand-tan-shadow;
  border-radius: variables.$pages-curves;
  span {
    font-size: 1.2rem;
    font-weight: 700;
    color: colors.$night-blue-shadow;
  }
}
.sub-status {
  font-size: 1.2rem;
  font-weight: 600;
  + span {
    color: chartreuse;
  }
}
.subscription-overlay {
  height: 100%;
  place-items: center;
}
.subscription-options {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}
.subscription-item {
  display: grid;
  place-items: center;
  height: 50vh;
  width: 45%;
  font-size: 0.8rem;
  font-weight: bold;
  background-color: #33423d;
  box-shadow: 2px 3px 0px #312a2a;
  border-radius: 0.5rem;
  color: black;
  cursor: pointer;
  .title {
    font-size: 1.5rem;
  }
  p {
    font-size: 0.6rem;
    font-weight: 400;
    text-align: center;
    color: bisque;
  }
  .amount {
    font-size: 2rem;
  }
  button {
    height: 3.5rem;
    width: 100%;
  }
  &:hover {
    background-color: #435650;
  }
  @media only screen and (min-width: 600px) {
    @media only screen and (min-height: 500px) {
      padding: 0.4rem 2rem;
    }
  }
}
.payments-icons {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 3rem;
  background-color: colors.$sand-tan;
  border-radius: 1rem;
}
