.listed-players {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 1rem;
  width: 20vw;
  border-radius: 1rem;
  padding: 0.6rem;
  font-size: 0.8rem;
  background-color: #c6ae82;
  box-shadow: inset 0px 0px 8px black;
  z-index: 200;
  h4 {
    margin-bottom: 0.4rem;
  }
}
.listed-player {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 8fr 1fr;
  align-items: center;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  background-color: rgb(155, 142, 122);
  border-radius: 1rem;
  padding: 0.2rem 0.6rem;
  margin-bottom: 0.2rem;
  .cancel {
    display: grid;
    place-items: center;
    background-color: red;
    width: 1rem;
    height: 1rem;
    border-radius: 0.5rem;
    font-size: 0.6rem;
    font-weight: 600;
    cursor: pointer;
    color: white;
  }
}
.cancel:hover .tool-tip,
.approve:hover .tool-tip {
  visibility: visible;
  &:hover {
    visibility: hidden;
  }
}
