@use '../colors';

.room {
  display: grid;
  place-self: center;
  place-items: center;
  height: 100%;
  width: 100%;
  .ribbon {
    grid-area: ribbon;
    display: flex;
    background: colors.$nav-color;
  }
}
.poker-table {
  position: absolute;
  z-index: -1;
  margin-left: auto;
  margin-right: auto;
  display: grid;
  width: 100%;
  height: 100%;
  transform: rotateX(24deg);
  border-radius: 40vh;
  background-color: #2d2616;
  box-shadow: inset 0 0 30px 0px black;
  .tsoxa {
    place-self: center;
    height: 90%;
    width: 95%;
    border-radius: 40vh;
    box-shadow: inset 0 0 18px 0px black;
    background: colors.$main-color;
  }
}
