.frame {
  display: flex;
  flex-direction: column;
  width: clamp(150px, 65vw, 1000px);
  padding: 1rem;
  border-radius: 1rem;
  background-color: rgb(24 142 125);
  box-shadow: inset 0 0 35px black;
  color: bisque;
  @media only screen and (min-height: 500px) {
    gap: 0.3rem;
  }
}
.frame-item {
  display: flex;
  align-items: center;
  padding: 0.4rem 2rem;
  font-size: 0.8rem;
  background-color: #33423d;
  border-radius: 7px;
  box-shadow: 2px 3px 0px #312a2a;
}
