@use '../../variables.scss';
@import './DecorationLayouts.scss';

.card-decoration {
  @include decoration-content-layout;
  position: relative;
  display: none;
  place-items: center;
  height: 86%;
  align-self: flex-end;
  flex: 1 0;
  border-radius: 0.3rem;
  @media only screen and (min-height: 500px) and (min-width: 750px) {
    display: grid;
  }
  svg {
    width: calc(0.17 * var(--card-width));
    height: calc(0.17 * var(--card-width));
  }
  .silhouette-suit {
    position: absolute;
    width: calc(0.19 * var(--card-width));
    height: calc(0.19 * var(--card-width));
    &:first-child {
      top: 1px;
      left: 1px;
    }
    &:nth-child(2) {
      bottom: 1px;
      right: 1px;
      transform: rotateZ(180deg);
    }
  }
  .ofc-logo {
    font-size: 0.3rem;
  }
}
