@use '../../colors.scss';
@use '../../variables.scss';

.account {
  background-color: colors.$sand-tan-shadow;
  border-radius: variables.$pages-curves;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  form {
    position: relative;
    input:hover {
      background-color: rgb(206, 184, 158);
    }
  }
  input {
    height: 1.3rem;
    font-size: 1rem;
  }
  svg {
    fill: #f56700;
    width: 1.3rem;
    height: 1.3rem;
    margin-left: 1rem;
    cursor: pointer;
    &:hover {
      fill: #c65402;
    }
  }
  span {
    color: #f56700;
    margin: 0.2rem;
  }
}
.content {
  display: flex;
  justify-content: center;
  flex: 1 0;
  align-items: center;
  label {
    font-size: 0.7rem;
  }
}
.title {
  width: 12rem;
  font-size: 0.8rem;
  font-weight: 600;
}
.last-game {
  padding: 0.3rem 0.6rem;
  border-radius: 0.3rem;
  cursor: pointer;
  background-color: #6b6359;
  &:hover {
    background-color: #d77a00;
  }
}
.config {
  display: flex;
  flex-direction: column;
  @media only screen and (min-height: 500px) {
    gap: 1rem;
  }
}
.deck-color {
  display: grid;
  grid-template-columns: 1fr repeat(4, 0.25fr);
  place-items: center;
  color: bisque;
  font-size: 0.75rem;
  input[name='deckColor'] {
    &:nth-child(2) {
      background-color: rgb(255 0 0 / 80%);
    }
    &:nth-child(3) {
      background-color: rgb(168 0 234 / 81%);
    }
    &:nth-child(4) {
      background-color: rgb(0 116 234 / 81%);
    }
    &:nth-child(5) {
      background-color: rgb(255 143 0 / 92%);
    }
  }
}
.hideInfo {
  display: flex;
  align-items: center;
}
.logout {
  display: flex;
  align-items: center;
  svg {
    margin: 0;
    margin-right: 0.3rem;
    fill: white;
    width: 1rem;
  }
}
