$board-width: calc(9.5 * var(--card-width));
$player-height: calc(1.7 * var(--card-height));
// hand cards shifting
$even-core: calc(0.125 * var(--card-width));
$even-mid: calc(0.375 * var(--card-width));
$even-outer: calc(0.625 * var(--card-width));
$odd-core: calc(0.25 * var(--card-width));
$odd-mid: calc(0.5 * var(--card-width));
$odd-outer: calc(0.75 * var(--card-width));
//selected card styling
$selected-shift: calc(-0.2 * var(--card-height));
$selected-shadow: calc(0.1 * var(--card-height)) #3fa603;
$selected-shadow-border: calc(0.1 * var(--card-height) + 1px) black;
$pages-curves: 70px 0 70px 0;
