@use '../../variables.scss';
@use '../../colors.scss';

.saloon {
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  background-color: colors.$night-grey;
  border-radius: variables.$pages-curves;
  padding: 1rem;
  @media only screen and (min-width: 900px) {
    justify-content: space-evenly;
  }
}
.player-tables {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 55vw;
}
