@use './colors.scss';
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,600,700|Yellowtail:400|Roboto:400,600}');

html {
  height: 100%;
  box-sizing: border-box;
  font-size: max(16px, 0.24 * var(--card-height));
}
*,
*:before,
*:after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
  &:focus {
    outline: none;
  }
}
:root {
  --card-height: 9.3vh;
  --card-width: 6.6vh;
  // --ribbon-height: 18vh;
  --info-buttons: 2.3vh;
}
@media only screen and (max-aspect-ratio: 1.31) {
  :root {
    --card-height: 6.8vw;
    --card-width: 4.9vw;
  }
}

/* input --> on autocomplete background color */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 1000px colors.$secondary-color inset !important;
  box-shadow: 0 0 0 1000px rgb(231, 150, 84) inset;
}
// custom scroll bar
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: bisque;
}
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 0.5rem;
}
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

body {
  height: 100%;
  background-color: #112423;
  font-family: Roboto, 'Helvetica Neue', sans-serif;
}
#root {
  display: grid;
  height: 100%;
}
img {
  max-width: 100%;
}
svg {
  display: block;
}
a {
  text-decoration: none;
}
.tool-tip {
  position: absolute;
  visibility: hidden;
  bottom: 100%;
  width: 7rem;
  background-color: bisque;
  color: black;
  font-size: 0.55rem !important;
  text-align: center;
  border-radius: 0.3rem;
  padding: 0.3rem;
  z-index: 11;
  @media only screen and (min-height: 500px) {
    @media only screen and (min-width: 650px) {
      font-size: 0.6rem !important;
    }
  }
}
.overlay {
  display: grid;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  padding: 3rem;
  z-index: 101;
  color: bisque;
  background-color: #00000066;
}
.snack-message {
  display: grid;
  place-items: center;
  place-self: center;
  padding: 2rem;
  border-radius: 0.5rem;
  white-space: pre-line;
  color: black;
  background-color: burlywood;
  box-shadow: inset 0 0 35px black;
  .spinner {
    width: auto;
    height: auto;
  }
}
.form-control {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: bisque;
  input:is([type='number'], [type='text']) {
    width: 100%;
    padding: 0.1rem;
    border-radius: 4px;
    border: 1px solid black;
    background-color: bisque;
    text-align: center;
  }
  input {
    &:focus-visible {
      outline: none;
    }
  }
  // remove number input arrows
  &::-webkit-outer-spin-button,
  ::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}
