@use '../../variables.scss';

.open-table {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 2px solid black;
  border-radius: 27px;
  padding: 0 1rem;
  box-shadow: inset 0 0 35px black;
  @media only screen and (min-width: 900px) {
    padding: 1rem;
  }
  form {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    border-radius: 0 0 1rem 1rem;
    background-color: rgb(37, 42, 45);
    button {
      padding: 0.75rem 1.5rem;
      font-weight: 600;
      @media only screen and (min-height: 500px) {
        margin: 0.8rem 0;
      }
    }
  }
  .form-control {
    width: 100%;
    display: grid;
    grid-gap: 0.4rem;
    margin-bottom: 0.3rem;
    input {
      font: inherit;
      &:not([class='checkbox']):hover {
        background-color: #e1c8ab;
      }
    }
    label {
      justify-self: flex-end;
      font-size: 0.75rem;
      cursor: pointer;
    }
    select {
      height: 1.5rem;
      text-align: center;
      border-radius: 4px;
      background-color: bisque;
      font-size: 0.9rem;
    }
    @media only screen and (min-width: 900px) {
      grid-template-columns: 1fr 1fr;
    }
  }
  span {
    color: red;
    font-size: 0.9rem;
    font-weight: 600;
    white-space: pre-line;
    text-align: center;
  }
  .form-inputs {
    display: flex;
    flex-direction: column;
    gap: 0.3rem;
    width: 100%;
    padding: 1rem;
    .form-control {
      margin: 0;
    }
  }
  .title {
    width: 100%;
    text-align: center;
    background-color: rgb(37 42 45);
    border-radius: 1rem 1rem 0 0;
    h3 {
      border-radius: 1rem 1rem 0 0;
      background-color: rgb(141 125 106);
      @media only screen and (min-height: 500px) {
        padding: 1rem;
      }
    }
  }
}
.rotation {
  display: grid;
  grid-template-columns: 1fr 0.5fr 0.5fr;
  align-items: center;
  color: bisque;
  font-size: 0.75rem;
  & > div {
    justify-self: flex-end;
  }
  label {
    display: flex !important;
    flex-direction: row-reverse;
    justify-content: center;
  }
}
svg {
  cursor: pointer;
}
svg[id='counterclock'] {
  transform: rotateY(180deg);
}
