@use '../../colors.scss';
@use './AccountLogedIn.scss';

.header-container {
  grid-area: header;
  height: 8vh;
  display: flex;
  padding: 0 2rem;
  justify-content: space-between;
  align-items: center;
  background-color: colors.$nav-color;
  z-index: 7;
  position: relative;
}
.header-logo {
  color: rgb(255 81 0);
  font-weight: 600;
  font-size: 1.5rem;
}
.nav-items {
  display: flex;
  align-items: center;
  font-size: 0.8rem;
  gap: 1rem;
}
.nav-link {
  color: rgb(191, 173, 138);
  &.active,
  &:hover {
    color: bisque;
  }
}
.log-btns {
  display: grid;
  place-items: center;
  height: 2rem;
  width: 5rem;
  border-radius: 1rem;
  font-size: 0.8rem;
  color: bisque;
  border: 3px solid bisque;
  cursor: pointer;
  &:hover,
  &.active {
    background: colors.$sand-tan;
    color: black;
  }
}
.account-nav {
  display: flex;
  align-items: center;
  position: relative;
}
.account-icon {
  cursor: pointer;
  height: 100%;
  display: flex;
  align-items: center;
  svg {
    fill: colors.$secondary-color;
    width: 1.5rem;
    height: 1.5rem;
    &:hover {
      fill: bisque;
    }
  }
}
