.spinner {
  display: grid;
  width: 10rem;
  height: 10rem;
  &:after {
    content: ' ';
    display: block;
    place-self: center;
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    margin: 0.6rem;
    border: 6px solid;
    border-color: inherit;
    animation: spinner 1.2s linear infinite;
  }
  &.centered {
    position: absolute;
    place-self: center;
  }
}

@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
