.table {
  display: grid;
  justify-content: space-evenly;
  grid-row-gap: 0.5rem;
  width: 100%;
  max-width: 1100px;
  &.players-2 {
    grid-template:
      'other host' max-content
      'hand hand' 1fr;
    grid-template-columns: 1fr 1fr;
  }
  &.players-3 {
    grid-template:
      'player-1 player-2' max-content
      'player-0 player-0' max-content
      'hand hand' 1fr;
    grid-template-columns: 1fr 1fr;
  }
  & + button {
    margin: 7px 0;
  }
}
.round {
  position: fixed;
  top: 29px;
  left: 17px;
  color: bisque;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sound-icon {
  position: fixed;
  bottom: 29px;
  left: 29px;
  width: 35px;
  height: 35px;
}
