.players-info {
  display: grid;
  justify-content: center;
  height: 100%;
  color: black;
  .listed-players {
    align-self: flex-end;
    &:nth-child(2) {
      align-self: flex-start;
    }
  }
  @media only screen and (min-height: 500px) {
    grid-template-rows: 1fr 1fr;
  }
}
.dealer-overlay {
  display: flex;
  flex-direction: column;
  button {
    padding: 1rem;
  }
}
