.board-layout {
  display: grid;
  justify-self: center;
  grid-template-columns: repeat(2, max-content);
  grid-template-rows: repeat(4, max-content);
  grid-template-areas:
    'ribbon empty '
    'board  score-details-0'
    'board  score-details-1'
    'board  score-details-2';
  grid-row-gap: 7px;
  max-width: 1100px;
  margin-bottom: 7px;
}
.board-grid {
  position: relative;
  display: grid;
  grid-area: board;
  grid-template-areas:
    'timer . . . score '
    '.     . . . .     '
    '.     . . . .     ';
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 0.3rem;
  & + button {
    margin: 7px 0;
  }
  justify-self: center;
}
.image-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0.6;
  svg {
    width: calc(3 * var(--card-height));
  }
}
.timer {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: large;
  font-weight: 700;
  color: bisque;
  background-color: black;
  border-radius: 50%;
  width: 62px;
  height: 50px;
  align-self: center;
  justify-self: center;
}
