.sitback {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  align-self: center;
  justify-self: center;
  height: 20vh;
  width: 35vw;
  padding: 0.3rem;
  box-shadow: 0px 0px 20px 8px rgb(252 0 0 / 67%);
  border-radius: 6px;
  background-color: rgb(30 29 27);
  color: bisque;
  button {
    width: 12vw;
    padding: 0.75rem 0;
    font-weight: 600;
    font-size: 0.8rem;
  }
}
.sitout {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 111;
  svg {
    width: 2rem;
    height: 3rem;
    fill: #d00303;
    cursor: pointer;
  }
  .tool-tip {
    right: 100%;
    bottom: 50%;
    transform: translateY(50%);
    background-color: red;
    border: 2px solid black;
    color: white;
    cursor: pointer;
  }
  &:hover .tool-tip {
    visibility: visible;
  }
  svg {
    fill: red;
  }
}
